import type { AuthState } from '@wix/cli-auth';
import {
  installNpmPackage,
  installAllPackages,
  resolveNpmDependencies,
} from '../dependency-manager/index.js';
import type { PackageManager } from '../dependency-manager/index.js';
import type { Logger } from '../logger.js';
import type { ProjectModel } from '../model.js';

export async function installImpl(
  packageName: string | null,
  model: ProjectModel,
  authState: AuthState,
  packageManager: PackageManager,
  logger: Logger
) {
  let logInstalled: undefined | (() => void);

  try {
    if (packageName) {
      logInstalled = logger.logDependencyInstalling(packageName, null);
      await installNpmPackage(
        packageName,
        null,
        model.projectFolder,
        packageManager
      );
    } else {
      logInstalled = logger.logAllDependenciesInstalling();
      await installAllPackages(model.projectFolder, packageManager);
    }

    await resolveNpmDependencies(model.projectFolder, { authState });
  } finally {
    if (logInstalled) {
      logInstalled();
    }
  }
}
