import { DEPENDENCIES_CONFIG_FILENAME } from '@wix/cli-site-definitions';
import path from 'node:path';
import { pathExists } from '@wix/cli-fs';
import fs from 'node:fs';
import { addDependenciesToPackageJson } from '../files/package-json.js';
import { loadDependenciesConfig } from '../files/velo.dependencies.js';
import type { Logger } from '../logger.js';

export const migrateToAnyNpmIfNeeded = async (root: string, logger: Logger) => {
  const oldDependenciesFilePath = path.join(
    root,
    'src',
    DEPENDENCIES_CONFIG_FILENAME
  );
  if (await pathExists(oldDependenciesFilePath)) {
    try {
      logger.logMigratingToAnyNpm();

      const oldVeloDeps = await loadDependenciesConfig(root);
      await addDependenciesToPackageJson(root, oldVeloDeps);
      await fs.promises.unlink(oldDependenciesFilePath);

      logger.logMigrationToAnyNpmCompletedSuccessfully();
    } catch (ex: unknown) {
      logger.logMigrationToAnyNpmFailed(ex);
      throw ex;
    }
  }
};
