import { cwd } from 'node:process';
import type { CommandServices } from '@wix/cli-command-contract';
import { getDefaultPackageManager } from '../dependency-manager/index.js';
import { createI18nT } from '../i18n.js';
import { createLogger } from '../logger.js';
import type { PackageManager } from '../dependency-manager/index.js';
import { installImpl as anyNpmInstallImpl } from '../velo-npm/anyNpm.js';
import { loadProjectModel } from '../model.js';
import { authenticate } from '../authentication.js';
import { migrateToAnyNpmIfNeeded } from '../velo-npm/anyNpmMigration.js';

export async function install(
  { biLogger, errorReporter }: CommandServices,
  options: {
    yarn?: boolean;
    npm?: boolean;
  },
  initialPackageName?: string
) {
  const model = await loadProjectModel(cwd());
  const authState = await authenticate({
    metaSiteId: model.metaSiteId,
    biLogger,
    errorReporter,
  });

  const t = createI18nT();
  const logger = createLogger({ t });
  const packageName = initialPackageName?.trim() ?? null;

  let packageManager: PackageManager;
  if (options.yarn) {
    packageManager = 'yarn';
  } else if (options.npm) {
    packageManager = 'npm';
  } else {
    packageManager = await getDefaultPackageManager(model.projectFolder);
  }

  await migrateToAnyNpmIfNeeded(model.projectFolder, logger);
  await anyNpmInstallImpl(
    packageName,
    model,
    authState,
    packageManager,
    logger
  );
}
