import { join } from 'node:path';
import type { DependenciesConfig } from '@wix/cli-site-definitions';
import {
  DEPENDENCIES_CONFIG_FILENAME,
  dependenciesConfigSchema,
} from '@wix/cli-site-definitions';
import { readJson, writeJson, pathExists } from '@wix/cli-fs';
import { CliError, CliErrorCode } from '@wix/cli-error';

const DEPENDENCIES_CONFIG_FILEPATH = join('src', DEPENDENCIES_CONFIG_FILENAME);

export async function saveDependenciesConfig(
  projectFolder: string,
  config: DependenciesConfig
) {
  await writeJson(join(projectFolder, DEPENDENCIES_CONFIG_FILEPATH), config, {
    spaces: 2,
  });
}

export async function dependenciesConfigExists(projectFolder: string) {
  return pathExists(join(projectFolder, DEPENDENCIES_CONFIG_FILEPATH));
}

export async function loadDependenciesConfig(projectFolder: string) {
  const configFile = await readJson(
    join(projectFolder, DEPENDENCIES_CONFIG_FILEPATH)
  );

  try {
    return dependenciesConfigSchema.parse(configFile);
  } catch (e) {
    throw new CliError({
      code: CliErrorCode.InvalidVeloConfigJson({
        configFilename: DEPENDENCIES_CONFIG_FILEPATH,
        cause: e,
      }),
    });
  }
}
